import React from "react";
import styled from "styled-components";

const PortfolioContainer = styled.div`
  margin: 0 auto;
  margin-right: 1%;
  text-align: center;
  width: 100%;
  color: white;
  background-color: red;

  transition: width 1s;

  grid-column-start: 2;
  grid-row-start: 1;
  grid-row-end: 2;
`;

export const Portfolio = () => {
  return (
    <PortfolioContainer id="portfolio_container">
      <h1>포트폴리오</h1>
    </PortfolioContainer>
  );
};
