import React, { useState } from "react";
import styled from "styled-components";

const ProfileContainer = styled.div`
  display: flex;
  flex-direction: column;
  color: white;
  background-color: rgba(255, 255, 255, 0.1);

  transition: width 1s;

  margin: auto;
  text-align: left;
  width: 100%;
  height: 100%;

  grid-column-start: 1;
  grid-row-start: 1;
  grid-row-end: 3;
`;

const PhotoContainer = styled.div`
  flex: 1;
  width: 150px;
  height: 150px;
  background-color: red;

  margin: auto;
  margin-left: 10rem;
  margin-top: 1rem;
`;

const TextContainer = styled.div`
  flex: 1;
  margin: auto;
  margin-left: 10rem;
`;

export const Profile = () => {
  const [isOpen, setIsOpen] = useState(false);

  function profileClicked(event) {
    if (event.target.id !== "profile_img_container" && !isOpen) {
      const port = document.getElementById("portfolio_container");
      const blog = document.getElementById("blog_container");
      event.target.style.width = "200%";
      event.target.style.backgroundColor = "darkcyan";
      port.style.width = "3%";
      blog.style.width = "3%";

      setIsOpen(true);
    } else {
      const port = document.getElementById("portfolio_container");
      const blog = document.getElementById("blog_container");
      event.target.style.width = "100%%";
      event.target.style.backgroundColor = "rgba(255,255,255,0.1)";
      port.style.width = "100%";
      blog.style.width = "100%";

      setIsOpen(false);
    }
  }

  return (
    <ProfileContainer id="profile_container" onClick={profileClicked}>
      <PhotoContainer id="profile_img_container"></PhotoContainer>
      <TextContainer>
        <h2>Kwon Oh-Min</h2>
        <h3>Junior Front-End Developer</h3>
        <h4>안녕하세요. 주니어 개발자 권오민이라고 합니다.</h4>
      </TextContainer>
    </ProfileContainer>
  );
};
