import React from "react";
import styled from "styled-components";

const BlogContainer = styled.div`
  margin: 0 auto;
  margin-right: 1%;
  text-align: center;
  width: 100%;
  color: white;
  background-color: blue;

  transition: width 1s;

  grid-column-start: 2;
  grid-row-start: 2;
  grid-row-end: 3;
`;

export const Blog = () => {
  return (
    <BlogContainer id="blog_container">
      <h1>블로그</h1>
    </BlogContainer>
  );
};
